* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  background-color: #22232d;
  font-family: 'Inter', sans-serif;
  color: #fff;
  height: auto;
}


/* ================================================== */
/* Navbar */
.nav {
  display: flex;
  justify-content: space-around;
  background-color: #0e1323;
  font-size: 18px;
  height: 75px;
  align-items: center;
  
}

nav a {
  text-decoration: none;
  color: #fff;
  cursor: pointer;
}

.logo {
  font-family: 'Unbounded', cursive;
  font-weight: 600;
  font-size: 30px;
}

.nav-links {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  width: 60%;
  letter-spacing: 2px;
}

.about,
.project,
.tech,
.resume,
.contact {
  color: white;
  text-decoration: none;
  padding:  10px 10px;
  margin: 0 10px;
  cursor: pointer;
}

.nav-links a:hover {
  color: #7BE7FF;
}

.nav-links .active {
  color: #7BE7FF;
}

.mobile-menu {
  display: none;
}

@media screen and (max-width: 880px) {
  .nav {
    height: 68px;
  }

  .logo {
    font-size: 30px;
  }
  
  .nav-links {
    display: none;
  }

  .nav-links-mobile {
    display: block;
    position: absolute;
    list-style: none;
    left: 0;
    top: 68px;
    width: 100%;
    transition: all 0.5 ease-out;
    background-color: #0e1323;
    color: #fff;
    height: 100vh;
    z-index: 3;
  }

  .nav-links-mobile a:hover {
    color: #7BE7FF;
  }

  .about,
  .project,
  .tech,
  .resume,
  .contact {
    text-align: center;
    padding:  32px;
    width: 100%;
    transition: all 0.5 ease-out;
  }

  .mobile-menu {
    display: block;
    position: absolute;
    top: 23px;
    right: 22px;
    font-size: 27px;
    color: white;
    background-color: #0e1323;
    border: none;
  }
}

/* ======================================================== */

/* Section */
.section {
  padding: 3% 10% 0;
}

/* about resume contact */
.about-me,
.projects,
.skills,
.resume-me {
  padding: 0 10%;
}

/* Line */
.line-1 {
  width: 50%;
  margin: 80px auto 40px;
  border: 1px solid #C0DE08;
}
.line-2 {
  width: 30%;
  margin: 0 auto 50px;
  border: 1px solid #C0DE08;
}

/* Hero */
.jumbotron {
  display: flex;
  justify-content: space-around;
  font-family: 'Nunito Sans', sans-serif;
  padding: 40px 0;
}

.img-profile{
  display: flex;
  width: 30%;
  justify-content: flex-end;
} 

.img-profile img {
  width: 300px;
  height: 300px;;
  border-radius: 50%;
  border: 3px solid #C0DE08;
}

.jumbotron .my-name {
 width: 40%;
 padding: 60px 0 0;
 
}

.my-name h2{
  font-size: 30px;
  color: #C0DE08;
}

.my-name h1 {
  font-size: 40px;
  line-height: 50px;
}

.btn-show {
  margin-top: 22px;
  font-size: 18px;
  padding: 10px 30px;
  background-color: #1fb0fe;
  border: none;
  border-radius: 10px;
  font-weight: 500;
  letter-spacing: 1px;
  color: #fff;
  cursor: pointer;
  transition: background-color .5s;
}

.btn-show:hover {
  background-color: #076fa7;
}

.img-github {
  display: flex;
  justify-content: flex-end;
  padding-top: 50px;
}

.img-github img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  transition: transform .5s;
}

.img-github img:hover {
  border: 3px solid #C0DE08;
  transform: scale(1.1);
}



/* About me */

.title {
  font-size: 22px;
  color: #C0DE08;
  margin-bottom: 20px;
}

.description {
  font-size: 18px;
  line-height: 30px;
  text-align: justify;
}

/* Projects */
.projects .flex-column {
  flex-direction: column;
  text-align: center;
}

.projects .grid-container {
  display: grid;
  grid-template-columns: auto auto;
}

.projects .flex-row {
  padding: 20px 0;
}

.figure {
  flex-grow: 1;
}

.projects img {
  width: 350px;
  border: 3px solid #C0DE08;
  margin: 10px 0;
  cursor: pointer;
}

.projects img:hover {
  opacity: .6;
}

.title-project {
  margin-bottom: 5px;
  color: #7BE7FF;
}

figcaption {
  width: 260px;
  line-height: 30px;
  margin: auto;
  color: #999;
}

/* Skills */
.skills .container {
  width: 70%;
  margin: auto;
}

.skills h3 {
  padding: 20px 20px;
  color: #7BE7FF;
  font-size: 20px;
}

.skills .flex-row {
  display: flex;
  justify-content: space-between;
}

.skills .row-2{
  justify-content: space-around;
}

/* Footer */
.footer {
  background-color: #0e1323;
}

.footer p {
  padding: 15px 0;
  font-size: 20px;
  text-align: center;
}

.footer p a {
  text-decoration: none;
  color: #fff;
  padding-left: 10px;
}

.footer p a:hover {
  color: #7BE7FF;
}


/* Resume */
.resume-me .items {
  padding: 5px 50px;
  line-height: 32px;
  
}

.resume-me .item-list {
  padding-bottom: 20px;
  padding-left: 10px;
}

.resume-me .item-list h2 {
  font-size: 18px;
  font-weight: 400;
}

.resume-me .item-list h3 {
  color: #7BE7FF;
  font-size: 17px;
  font-weight: 400;
}

.resume-me .item-list p {
  font-size: 16px;
  color: #d9d9d9;
}

.resume-me .item-list p a {
  color: #fff;
  text-decoration: none;
}

.resume-me .item-list p a:hover {
  color: #C0DE08;
}

.line-resume {
  width: 60%;
  border: 1px dashed #C0DE08;
  margin: 50px auto;
}

/* Kontak */
.kontak {
  height: 70vh;
  width: 80%;
  margin: 50px auto;
}

.kontak .title {
  text-align: center;
}

.kontak hr {
  margin: auto;
  width: 80px;
}

.kontak .container {
  padding: 70px 0;
}

.kontak .flex-row {
  display: flex;
  justify-content: space-around;
}

.kontak .card {
  width: 230px;
  border: 1px solid #C0DE08;
  padding: 20px;
  text-align: center;
  line-height: 27px;
}

.kontak img {
  width: 50px;
  height: 50px;
}

.kontak a{
  color: #fff;
  text-decoration: none;
  transition: color .4s;
}

.kontak a:hover {
  color: #C0DE08;
}

@media screen and (max-width: 1230px) {
  .section {
    padding: 3% 5% 0;
  }
}

@media screen and (max-width: 1028px) {
  .section {
    padding: 3% 0% 0;
  }
  
  /* about resume contact */
  
  .projects {
    padding: 20px 0px;
  }

  .resume-me {
    padding: 20px 80px;
  }

  /* Jumbotron */
  .img-profile img {
    margin: auto;
    width: 250px;
    height: 250px;
  }

  .jumbotron .my-name {
    width: 50%;
   }

  .title {
    text-align: center;
   }
  
  .projects img:hover {
    opacity: .6;
  }
  
  figcaption {
    font-size: 16px;
    line-height: 28px;
  }
}

@media screen and (max-width: 880px) {
  .kontak {
    height: 800px;
    margin: 20px auto;
  }

  .kontak .container {
    padding: 50px 0 20px;
  }

  .kontak .flex-row {
    display: block;
  }

  .kontak .card {
    margin: 0 auto 30px;
    
  }
}

/* Mobile Version */
@media screen and (max-width: 855px) {
  /* Jumbotron */
  .jumbotron {
    display: block;
    text-align: center;
  }

  .img-profile{
    display: block;
    width: 100%;
  }

  .img-profile img {
    margin: auto;
    width: 230px;
    height: 230px;
  }

  .jumbotron .my-name {
    width: 100%;
    padding: 40px 0;
  }

  .my-name h2{
  font-size: 27px;
  }
  
  .my-name h1 {
    font-size: 31px;
  }

  .img-github {
    display: none;
  }

  /* Line */
  .line-1 {
    margin: 30px auto 20px;
  }
  .line-2 {
    margin: 0 auto 40px;
  }

  /* About me */
  .description {
    font-size: 15px;
  }

  /* Projects */
  .projects .flex-column {
    display: block;
  }
  
  .projects .flex-row {
    display: block;
    padding: 0px 0px;
   }
  
  .figure {
    padding: 20px 0;
  }

  /* Skills */
  .skills {
    height: auto;
  }
  .skills .container {
    width: 100%;
  }

  .skills h3 {
    padding: 10px 20px;
    font-size: 18px;
  }

  .skills .flex-row {
    justify-content: space-around;
  }

  /* Resume */
  .resume-me {
    padding: 20px 40px;
  }

  .resume-me .item-list h2 {
    font-size: 16px;
  }
  
  .resume-me .item-list h3 {
    font-size: 15px;
  }
  
  .resume-me .item-list p {
    font-size: 14px;
  }

  .line-resume {
    margin: 20px auto;
  }
}


@media screen and (max-width: 510px) {
  .resume-me {
    padding: 20px 15px;
  }
}

